import React from "react"
import styled from "styled-components"

const TimeLineContainer = styled.ul`
  list-style: none;
  margin: 0;
  position: absolute;
  left: 47%;

  > li {
    position: relative;
    height: 320px;

    &:before {
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      border-radius: 100%;
      background-color: ${props => props.theme.colours.primary};
    }

    &:after {
      position: absolute;
      top: 0;
      left: 44%;
      content: "";
      border-left: 2px solid ${props => props.theme.colours.primary};
      height: 100%;
    }

    &:first-of-type:after {
      top: 0%;
    }

    &:last-of-type:after {
      top: -100%;
    }
  }

  @media only screen and (max-width: 950px) {
    display: none;
  }
`

const Timeline = () => (
  <TimeLineContainer>
    <li />
    <li />
    <li />
    <li />
    <li />
  </TimeLineContainer>
)

export default Timeline
