import React from "react"
import styled, { keyframes, css } from "styled-components"
import ScrollAnimation from "react-animate-on-scroll"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Header from "../components/header"
import Timeline from "../components/timeline"

const PageContainer = styled.div`
  background-color: ${props => props.theme.colours.secondary};
  padding-top: ${props => props.theme.spacing.xxl};
`

const H1 = styled.h1`
  text-align: center;
  color: #ffffff;
  margin: ${props => props.theme.spacing.xxl} 0;
  font-size: ${props => props.theme.fontSize.font80};

  @media only screen and (max-width: 600px) {
    font-size: ${props => props.theme.fontSize.font48};
  }
`

const Text = styled.span`
  display: block;
  font-size: ${props =>
    props.large ? props.theme.fontSize.font24 : props.theme.fontSize.font19};
  margin-bottom: ${props => props.theme.spacing[props.mb]};
  color: ${props => {
    if (props.primary) {
      return props.theme.colours.primary
    } else if (props.tertiary) {
      return props.theme.colours.tertiary
    } else {
      return "#ffffff"
    }
  }};
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  @media only screen and (max-width: 950px) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: ${props => props.theme.spacing.lg};
  }
`

const EmptyGridItem = styled.div`
  @media only screen and (max-width: 950px) {
    display: none;
  }
`

const FadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`

const WorkExperienceWrapper = styled.div`
  visibility: hidden;

  ${props => {
    if (props.isTextAnimationFinished) {
      return css`
        visibility: visible;
        animation: ${FadeIn} 0.5s ease-in;
      `
    }
  }};
`

const WorkExperience = styled(ScrollAnimation).attrs({
  animateOnce: true,
  animateIn: "fadeIn",
})`
  height: 320px;
  text-align: ${props => (props.rightAlign ? "right" : "left")};
  margin: 0 ${props => props.theme.spacing.xxl};

  @media only screen and (max-width: 950px) {
    text-align: left;
    margin: 0 ${props => props.theme.spacing.md};
  }
`

class Work extends React.Component {
  state = { typeAnimationText: "", isTextAnimationFinished: false }

  textToAnimate = "Experience."

  componentDidMount() {
    this.typeAnimation(this.textToAnimate)
  }

  typeAnimation = text => {
    const { typeAnimationText } = this.state
    if (typeAnimationText.length !== text.length) {
      setTimeout(() => {
        this.setState({
          typeAnimationText: text.substring(0, typeAnimationText.length + 1),
        })
        this.typeAnimation(text, typeAnimationText.length + 1)
      }, 75)
    } else {
      this.setState({ isTextAnimationFinished: true })
    }
  }

  render() {
    const { isTextAnimationFinished } = this.state

    return (
      <Layout>
        <SEO title="Work" />
        <Header transparent location={this.props.location.pathname} />

        <PageContainer>
          <H1>{this.state.typeAnimationText}</H1>

          <WorkExperienceWrapper
            isTextAnimationFinished={isTextAnimationFinished}
          >
            <Timeline />

            <Grid>
              <EmptyGridItem />

              <WorkExperience>
                <Text mb="md" primary>
                  Feb 2018 - June 2019
                </Text>
                <Text mb="lg" large>
                  PERSONABLY
                </Text>
                <Text mb="sm">Software Engineer</Text>
                <Text tertiary style={{ fontWeight: 300 }}>
                  Joining as the first employee, I worked across the whole code
                  base from a very early stage, which included NodeJS, GraphQL
                  and React.
                </Text>
              </WorkExperience>

              <WorkExperience rightAlign>
                <Text mb="md" primary>
                  April 2017 - Jan 2018
                </Text>
                <Text mb="lg" large>
                  JUSTGIVING
                </Text>
                <Text mb="sm">Junior Frontend Developer</Text>
                <Text tertiary style={{ fontWeight: 300 }}>
                  Solely focusing on frontend development, I worked closely with
                  designers to build projects in React, Redux and TypeScript.
                </Text>
              </WorkExperience>

              <EmptyGridItem />
              <EmptyGridItem />

              <WorkExperience>
                <Text mb="md" primary>
                  Aug 2016 - March 2017
                </Text>
                <Text mb="lg" large>
                  SELF EMPLOYED
                </Text>
                <Text mb="sm">Freelance Full Stack Developer</Text>
                <Text tertiary style={{ fontWeight: 300 }}>
                  Working on a variety of fullstack JavaScript projects, lasting
                  anytime between a few days to a few months, I developed the
                  skills I need for full time role.
                </Text>
              </WorkExperience>

              <WorkExperience rightAlign>
                <Text mb="md" primary>
                  Aug 2015 - Aug 2016
                </Text>
                <Text mb="lg" large>
                  FOUNDERS AND CODERS
                </Text>
                <Text mb="sm">Full JavaScript Stack Developer</Text>
                <Text tertiary style={{ fontWeight: 300 }}>
                  Joining the sixith cohort, I completed a three month intensive
                  full stack JavaScript course and then went on to join their
                  freelance cooperative.
                </Text>
              </WorkExperience>

              <EmptyGridItem />
              <EmptyGridItem />

              <WorkExperience>
                <Text mb="md" primary>
                  Sep 2011 - July 2014
                </Text>
                <Text mb="lg" large>
                  UNIVERSITY OF EXETER
                </Text>
                <Text mb="sm">Student</Text>
                <Text tertiary style={{ fontWeight: 300 }}>
                  Achieved 2:1 in Mathematics and Philosophy
                </Text>
              </WorkExperience>
            </Grid>
          </WorkExperienceWrapper>
        </PageContainer>
      </Layout>
    )
  }
}

export default Work
